import { ChangeDetectionStrategy, Component, Input } from '@angular/core';

export enum ResultType {
    Resolution,
    System,
    Processor,
    Memory,
    Storage,
    Internet,
    Servers
}

export interface Result {
    /** Unique type of the result, used to differentiate between result entries. */
    type: ResultType;
    label: string;
    value: string;
    failMessage: string;
    isPass: boolean;
    // tslint:disable-next-line: no-any
    params?: any;
}

enum Status {
    Ready,
    NotReady,
    Offline
}

@Component({
    selector: 'app-table',
    templateUrl: './table.component.html',
    styleUrls: ['./table.component.scss'],
    changeDetection: ChangeDetectionStrategy.OnPush
})
export class TableComponent {
    @Input()
    public results?: Result[];

    public get isPass() {
        return this.readyState === Status.Ready;
    }

    public get isNotOffline() {
        return this.readyState !== Status.Offline;
    }

    public get readyState() {
        if (!this.results || !this.results.length) {
            return Status.NotReady;
        }

        if (this.results.every((option: Result) => option.isPass)) {
            return Status.Ready;
        }

        return this.results
            .filter(result => result.type !== ResultType.Internet && result.type !== ResultType.Servers)
            .every((option: Result) => option.isPass)
            ? Status.Offline
            : Status.NotReady;
    }

    public get readyMessage() {
        switch (this.readyState) {
            case Status.Ready:
                return 'The device is ready.';
            case Status.Offline:
                return 'This device should be prepared for offline mode.';
            case Status.NotReady:
                return 'The device is not ready.';
        }
    }
}
