import { ChangeDetectionStrategy, Component, Input } from '@angular/core';

@Component({
    selector: 'app-status',
    templateUrl: './status.component.html',
    styleUrls: ['./status.component.scss'],
    changeDetection: ChangeDetectionStrategy.OnPush
})
export class StatusComponent {
    @Input()
    public withCircle?: boolean;

    @Input()
    public isPass?: boolean;

    @Input()
    public size?: number;

    public get icon() {
        if (this.isPass) {
            return this.withCircle ? 'check-circle-1' : 'check-1';
        } else {
            return this.withCircle ? 'remove-circle' : 'close';
        }
    }

    public get colorClass() {
        return this.isPass ? 'pass' : 'fail';
    }

    public get circleClass() {
        return this.withCircle ? 'circle' : '';
    }

    public get sizeStyle() {
        const size = this.size || 24;

        return { 'width.px': size, 'height.px': size };
    }
}
