import { ChangeDetectionStrategy, ChangeDetectorRef, Component, OnDestroy, OnInit } from '@angular/core';
import { MatDialog } from '@angular/material/dialog';
import filesize from 'filesize';
import { forkJoin, Observable, Subject, throwError } from 'rxjs';
import { catchError, finalize, map, takeUntil } from 'rxjs/operators';
import { SaveResult } from 'shared/results';
import { Software, SystemInfo } from 'shared/system-info';
import { MessageService } from 'src/app/shared/services/message.service';
import { SaveCompleteComponent } from './save-complete/save-complete.component';
import { SaveComponent, SaveData } from './save/save.component';
import { Result, ResultType } from './table/table.component';

/** 1 gigabyte in bytes. */
const gigabyteInBytes = 1e9;

@Component({
    selector: 'app-result',
    templateUrl: './result.component.html',
    styleUrls: ['./result.component.scss'],
    changeDetection: ChangeDetectionStrategy.OnPush
})
export class ResultComponent implements OnInit, OnDestroy {
    public error?: string;
    public isLoading = true;
    public inProgress = { systemInfo: true, connectionInfo: true };

    public results$: Observable<Result[]>;

    private readonly onDestroyed$ = new Subject<void>();

    private readonly connectionInfo$ = this.message.getConnectionInfo().pipe(
        catchError(error => {
            this.error = 'Failed to obtain system information. Please consult your administrator.';
            this.changeDetector.detectChanges();
            return throwError(error);
        }),
        map(connectionInfo => {
            const connections: Result[] = [
                {
                    type: ResultType.Internet,
                    isPass: connectionInfo.web,
                    label: 'Internet',
                    value: connectionInfo.web ? 'Successfully connected' : '',
                    failMessage: 'This device is not connected to the Internet.'
                }
            ];

            if (connectionInfo.connections.passed > 0 || connectionInfo.connections.failed > 0) {
                connections.push({
                    type: ResultType.Servers,
                    isPass: !connectionInfo.connections.failed,
                    label: 'Janison servers',
                    value: !connectionInfo.connections.failed
                        ? 'Successfully reached all required servers ({{passed}})'
                        : '',
                    params: {
                        total: connectionInfo.connections.passed + connectionInfo.connections.failed,
                        failed: connectionInfo.connections.failed,
                        passed: connectionInfo.connections.passed
                    },
                    failMessage: '{{failed}}/{{total}} servers could not be reached'
                });
            }

            return connections;
        }),
        finalize(() => {
            this.inProgress.connectionInfo = false;
            this.changeDetector.detectChanges();
        })
    );

    private readonly systemInfo$ = this.message.getSystemInfo().pipe(
        catchError(error => {
            this.error = 'Failed to obtain system information. Please consult your administrator.';
            this.changeDetector.detectChanges();
            return throwError(error);
        }),
        map(systemInfo => this.mapSystemInfo(systemInfo)),
        finalize(() => {
            this.inProgress.systemInfo = false;
            this.changeDetector.detectChanges();
        })
    );

    private isReady = false;
    private savedResults: Promise<SaveResult[]>;

    constructor(
        private readonly message: MessageService,
        private readonly changeDetector: ChangeDetectorRef,
        private readonly dialog: MatDialog
    ) {
        this.results$ = forkJoin([this.systemInfo$, this.connectionInfo$]).pipe(
            map(results => {
                const [systemInfo, connectionInfo] = results;
                const deviceResults = [...systemInfo, ...connectionInfo];

                this.isReady = deviceResults.every((option: Result) => option.isPass);

                return deviceResults;
            }),
            finalize(() => {
                this.isLoading = false;
                this.changeDetector.detectChanges();
            }),
            takeUntil(this.onDestroyed$)
        );
    }

    public ngOnInit(): void {
        // Preload the saved results so it's ready for the save modal.
        this.savedResults = this.getSavedResults();
    }

    public ngOnDestroy(): void {
        this.onDestroyed$.next();
        this.onDestroyed$.complete();
    }

    public save() {
        const data: SaveData = {
            isReady: this.isReady,
            previousResults: this.savedResults
        };

        this.dialog
            .open<SaveComponent, SaveData, boolean>(SaveComponent, { width: '500px', autoFocus: false, data })
            .afterClosed()
            .subscribe(isSaved => {
                if (isSaved) {
                    this.dialog.open(SaveCompleteComponent, { width: '300px', autoFocus: false });
                }
            });
    }

    private async getSavedResults() {
        return this.message.getResults().toPromise();
    }

    private mapSystemInfo(systemInfo: SystemInfo): Result[] {
        return [
            {
                type: ResultType.Resolution,
                isPass: this.isDisplayPass(systemInfo.display.resolutionX, systemInfo.display.resolutionY),
                label: 'Display resolution',
                value: `${systemInfo.display.resolutionX} x ${systemInfo.display.resolutionY}px`,
                failMessage: `A minimum resoluton of 1024px x 767px is required.`
            },
            {
                type: ResultType.System,
                isPass: this.isSoftwarePass(systemInfo.software),
                label: 'Operating System',
                value: systemInfo.software.name,
                failMessage: 'Windows 7 with Service pack 1 is required at minimum.'
            },
            {
                type: ResultType.Processor,
                isPass: this.isProcessorPass(+systemInfo.processor.speed),
                label: 'Processor',
                value: `${systemInfo.processor.manufacturer} ${systemInfo.processor.brand} ${systemInfo.processor.speed}GHZ`,
                failMessage: 'A 1GHZ processor is required at minimum.'
            },
            {
                type: ResultType.Memory,
                isPass: this.isMemoryPass(systemInfo.memory.total),
                label: 'Memory',
                value: '{{total}} RAM',
                params: { total: this.formatRam(systemInfo.memory.total) },
                failMessage: 'A minimum of 1GB RAM is required.'
            },
            {
                type: ResultType.Storage,
                isPass: this.isStoragePass(systemInfo.storage.available),
                label: 'Storage',
                value: '{{available}} of {{total}} available',
                params: this.formatStorage(systemInfo.storage.total, systemInfo.storage.available),
                failMessage: 'A minimum of 1GB of available hard drive space is required.'
            }
        ];
    }

    private formatRam(total: number) {
        return this.formatBytes(total);
    }

    private formatStorage(total: number, available: number) {
        const formattedTotal = this.formatBytes(total);
        const formattedAvailable = this.formatBytes(available);

        return { total: formattedTotal, available: formattedAvailable };
    }

    private isDisplayPass(x: number, y: number) {
        return x >= 1024 && y >= 767;
    }

    private isSoftwarePass(software: Software) {
        // Must be Windows.
        if (software.platform !== 'win32') {
            return false;
        }

        if (!software.build) {
            return false;
        }

        // https://docs.microsoft.com/en-us/windows/win32/api/winnt/ns-winnt-osversioninfoexw#remarks
        // https://www.lifewire.com/windows-version-numbers-2625171 for sp1 win7 version number
        const [major, minor, release] = software.build.split('.');
        /** Not valid if it's not Windows 7 SP1 or higher. */
        return (+major >= 6 && +minor >= 1 && +release >= 7601) || +major >= 10;
    }

    private isProcessorPass(speed: number) {
        return speed >= 1;
    }

    private isMemoryPass(memory: number) {
        return memory >= gigabyteInBytes;
    }

    private isStoragePass(available: number) {
        return available >= gigabyteInBytes;
    }

    private formatBytes(value: number) {
        return filesize(value, { round: 0 });
    }
}
