import { Message } from 'shared/message';

interface ElectronWindow extends Window {
    require(module: string): Electron.RendererInterface;
}

declare let window: ElectronWindow;
let _electron: Electron.RendererInterface;

function electron() {
    if (_electron) {
        return _electron;
    }

    if (window && window.require) {
        _electron = window.require('electron');
        return _electron;
    }

    return null;
}

/**
 * Send log message to electron, this will bypass all angular logic in case angular app crashes.
 */
export function log(message: string, stack?: string) {
    electron().ipcRenderer.send(Message.Log, message, stack);
}
