import { ChangeDetectionStrategy, ChangeDetectorRef, Component, Inject, OnInit } from '@angular/core';
import { FormControl, FormGroup, Validators } from '@angular/forms';
import { MAT_DIALOG_DATA, MatDialogRef } from '@angular/material/dialog';
import { SaveResult } from 'shared/results';
import { MessageService } from 'src/app/shared/services/message.service';

export interface SaveData {
    previousResults: Promise<SaveResult[]>;
    isReady: boolean;
}

@Component({
    selector: 'app-save',
    templateUrl: './save.component.html',
    styleUrls: ['./save.component.scss'],
    changeDetection: ChangeDetectionStrategy.OnPush
})
export class SaveComponent implements OnInit {
    public isLoading = true;
    public isSaving = false;
    public school = new FormControl('', Validators.required);
    public room = new FormControl('', Validators.required);
    public device = new FormControl('', Validators.required);

    public saveForm = new FormGroup({
        school: this.school,
        room: this.room,
        device: this.device
    });

    public error?: string;

    constructor(
        private readonly message: MessageService,
        private readonly changeDetector: ChangeDetectorRef,
        private readonly dialogRef: MatDialogRef<SaveComponent, boolean>,
        @Inject(MAT_DIALOG_DATA) private readonly data: SaveData
    ) {}

    public ngOnInit(): void {
        this.data.previousResults
            .then(savedResults => {
                const result = savedResults[savedResults.length - 1];

                // Pre-fill the school and room with the last result.
                if (result) {
                    this.school.patchValue(result.school);
                    this.room.patchValue(result.room);
                }
            })
            .finally(() => {
                this.isLoading = false;
                this.changeDetector.markForCheck();
            });
    }

    public async onSubmit() {
        if (this.isLoading || this.isSaving) {
            return;
        }

        this.error = undefined;
        this.isSaving = true;
        this.changeDetector.detectChanges();

        try {
            await this.message.saveResult({
                school: this.school.value,
                room: this.room.value,
                device: this.device.value,
                isReady: this.data.isReady
            });

            this.dialogRef.close(true);
        } catch (error) {
            this.error = 'Failed to save results. Please consult your administrator.';
            this.isSaving = false;
            this.changeDetector.detectChanges();
        }
    }
}
