import { Injectable } from '@angular/core';
import { TranslateService } from '@ngx-translate/core';
import { log } from 'src/app/shared/utils/log';

export enum LanguageId {
    English = 'en',
    Portuguese = 'pt',
    BrazilianPortuguese = 'pt-BR',
    Russian = 'ru',
    Spanish = 'es',
    Thai = 'th',
    Japanese = 'jp',
    Chinese = 'zh',
    Kazakhstan = 'kk',
    RussianKazakhstan = 'ru-KZ',
}

export interface Language {
    id: LanguageId;
    name: string;
}

@Injectable({ providedIn: 'root' })
export class LanguageService {
    public languages: Language[] = [
        { id: LanguageId.English, name: 'English' },
        { id: LanguageId.Portuguese, name: 'Portugus' },
        { id: LanguageId.BrazilianPortuguese, name: 'Portugus (Brasil)' },
        { id: LanguageId.Russian, name: 'P' },
        { id: LanguageId.Spanish, name: 'espaol' },
        { id: LanguageId.Thai, name: '' },
        { id: LanguageId.Japanese, name: '' },
        { id: LanguageId.Chinese, name: '' },
        { id: LanguageId.Kazakhstan, name: '' },
        { id: LanguageId.RussianKazakhstan, name: 'P ()' }
    ];

    public get onLanguageChange() {
        return this.translate.onLangChange;
    }

    public get currentLanguage(): LanguageId {
        return this.translate.currentLang as LanguageId;
    }

    constructor(private readonly translate: TranslateService) { }

    public init() {
        try {
            this.translate.setDefaultLang(LanguageId.English);

            const browserLanguage = this.languages.find(language => language.id === this.translate.getBrowserLang());
            this.translate.use(browserLanguage ? browserLanguage.id : LanguageId.English);
        } catch (error) {
            log(`Failed to init language. ${error.message}`, error.stack);
        }
    }

    public setLanguage(id: LanguageId) {
        this.translate.use(id);
    }
}
