import { ChangeDetectionStrategy, Component } from '@angular/core';
import { MatDialogRef } from '@angular/material/dialog';
import { MessageService } from 'src/app/shared/services/message.service';

@Component({
    selector: 'app-save-complete',
    templateUrl: './save-complete.component.html',
    styleUrls: ['./save-complete.component.scss'],
    changeDetection: ChangeDetectionStrategy.OnPush
})
export class SaveCompleteComponent {
    constructor(
        private readonly dialogRef: MatDialogRef<SaveCompleteComponent>,
        private readonly message: MessageService
    ) {}

    public continue() {
        this.dialogRef.close();
    }

    public exit() {
        this.message.exit().subscribe();
    }
}
