import { A11yModule } from '@angular/cdk/a11y';
import { CommonModule } from '@angular/common';
import { HttpClientModule } from '@angular/common/http';
import { NgModule } from '@angular/core';
import { MatButtonModule } from '@angular/material/button';
import { MatDialogModule } from '@angular/material/dialog';
import { MatDividerModule } from '@angular/material/divider';
import { MatFormFieldModule } from '@angular/material/form-field';
import { MatIconModule, MatIconRegistry } from '@angular/material/icon';
import { MatInputModule } from '@angular/material/input';
import { DomSanitizer } from '@angular/platform-browser';
import { log } from './utils/log';

const modules = [
    HttpClientModule,
    CommonModule,
    A11yModule,
    MatButtonModule,
    MatDialogModule,
    MatDividerModule,
    MatFormFieldModule,
    MatIconModule,
    MatInputModule
];

@NgModule({
    declarations: [],
    imports: modules,
    exports: modules,
    providers: []
})
export class MaterialModule {
    constructor(private readonly matIconRegistry: MatIconRegistry, private readonly domSanitizer: DomSanitizer) {
        try {
            const streamlineUrl = 'assets/icons.svg';
            const sanitizedUrl = this.domSanitizer.bypassSecurityTrustResourceUrl(streamlineUrl);

            /* istanbul ignore next: expected not to run during tests */
            this.matIconRegistry.addSvgIconSet(sanitizedUrl);
        } catch (error) {
            log(`Failed to load icons. ${error.message}`, error.stack);
        }
    }
}
