import { ChangeDetectionStrategy, ChangeDetectorRef, Component, OnInit, ViewEncapsulation } from '@angular/core';
import { LanguageId, LanguageService } from './language.service';

@Component({
    selector: 'app-language',
    templateUrl: './language.component.html',
    styleUrls: ['./language.component.scss'],
    changeDetection: ChangeDetectionStrategy.OnPush,
    encapsulation: ViewEncapsulation.None
})
export class LanguageComponent implements OnInit {
    constructor(public readonly languageService: LanguageService, private readonly changeDetector: ChangeDetectorRef) {}

    public ngOnInit(): void {
        this.languageService.onLanguageChange.subscribe(() => {
            this.changeDetector.markForCheck();
        });
    }

    public setLanguage(id: LanguageId) {
        this.languageService.setLanguage(id);
    }
}
