/*global window */
import { Injectable } from '@angular/core';
import { from } from 'rxjs';
import { ConnectionInfo } from 'shared/connection-info';
import { Message } from 'shared/message';
import { SaveResult } from 'shared/results';
import { SystemInfo } from 'shared/system-info';
import { log } from 'src/app/shared/utils/log';

/**
 * This message service is built for backends which implement the deviceCheck service
 *
 * The backend is required make availiable a function:
 *      window._deviceCheck.sendMessage(String.type, JSON.args, Promise.resolve, Promise.reject)
 * which processes the message and returns a JSON.response on the supplied promises
 */
@Injectable({ providedIn: 'root' })
export class MessageService {
    public getSystemInfo() {
        return this.send<SystemInfo>(Message.SystemInfo);
    }

    public getConnectionInfo() {
        return this.send<ConnectionInfo>(Message.Connection);
    }

    public getResults() {
        return this.send<SaveResult[]>(Message.Results);
    }

    public exit() {
        return this.send<void>(Message.Exit);
    }

    public async saveResult(data: SaveResult) {
        return this.send<void>(Message.SaveResults, data).toPromise();
    }

    private send<TResult>(request: Message, ...args: unknown[]) {
        // tslint:disable-next-line:no-any
        const result = new Promise<any>((pResolve, pReject) => {
            // tslint:disable-next-line:no-any
            (window as any)._deviceCheck.sendMessage(request, JSON.stringify(args), pResolve, pReject);
        });

        return from(result.catch(r => JSON.parse(r)).then(r => JSON.parse(r)));
    }
}
